#menu {
    width: 220px;
    bottom: 0;
    float: right;
    right: 0;
    top: 66px;
    transition: margin .4s ease 0s;
    height: calc(100vh - 66px);
    background-color: #fff;
    position: fixed;

    @media (max-width:767px) {
        width: 180px;
    }

    .profile {
        padding: 10px;
        text-align: center;

        .profile__img {
            width: 100px;
            border-radius: 50%;
            border: 1px solid #ccc;
        }

        .profile__name {
            color: #62cb31;
            margin-top: 10px;
            font-weight: 600;
            font-size: 20px;
        }

        .profile__desc {
            margin-top: 10px;
            font-size: 14px;
            font-weight: 500;
        }
    }

    .link {
        list-style-type: none;
        padding: 0;
        margin: 0;

        .link__item {
            background-color: aliceblue;
            border-top: 1px solid #eaeaea;
            background-color: #f7f6f68f;
            position: relative;

            a {
                color: #666;
                display: block;
                width: 100%;
                padding: 10px;
                text-decoration: none;
                font-size: 14px;
                font-weight: 600;

                &:hover {
                    background-color: #fff;
                }
            }

            ul {
                list-style: none;
                margin: 0;
                padding: 0;

                .link__item__submenu {

                    a {
                        padding: 10px 30px 10px 10px;
                        position: relative;

                        &::before {
                            content: "\f104";
                            font-family: FontAwesome;
                            font-style: normal;
                            font-weight: normal;
                            text-decoration: inherit;
                            /*--adjust as necessary--*/
                            color: #000;
                            font-size: 16px;
                            padding-right: 0.5em;
                            position: absolute;
                            top: 7px;
                            right: 10px;
                        }
                    }

                }
            }

            &.hasSubmenu::before {
                content: "\f107";
                font-family: FontAwesome;
                font-style: normal;
                font-weight: normal;
                text-decoration: inherit;
                /*--adjust as necessary--*/
                color: #000;
                font-size: 16px;
                padding-right: 0.5em;
                position: absolute;
                top: 7px;
                left: 10px;
            }
        }
    }
}