.login {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    .login__box {
        background-color: #fff;
        box-shadow: 0px 0px 1px #eee;
        border: 1px solid #eee;
        padding:20px 10px;
        border-top: 2px solid #62cb31;

        .login__box__icon {
            width: 80px;
            height: 80px;
            background-color: #eee;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            margin:auto;
            color: #62cb31;
            font-size: 30px;
            border:1px solid #62cb31;
        }

        .login__box__title {
            text-align: center;
            color: #62cb31;
            margin-top: 10px;
            font-weight: 600;
            font-size: 20px;
        }

        .login__box__link {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 10px;

            a {
                color: #62cb31;
                font-weight: 600;
            }

        }
    }
}