.color-line {
    background: #f7f9fa;
    height: 6px;
    position: fixed;
    width: 100%;
    top: 0;
    right: 0;
    z-index: 1;
    background-image: linear-gradient(to right,#34495e,#34495e 25%,#9b59b6 25%,#9b59b6 35%,#3498db 35%,#3498db 45%,#62cb31 45%,#62cb31 55%,#ffb606 55%,#ffb606 65%,#e67e22 65%,#e67e22 75%,#e74c3c 85%,#e74c3c 85%,#c0392b 85%,#c0392b 100%);
}
.header {
    height: 60px;
    background-color: #fff;
    box-shadow: 0px 0px 1px #eee;
    border-bottom: 1px solid #eee;
    position: fixed;
    top: 6px;
    right: 0;
    width: 100%;
    z-index: 1;

    .header__welcome {
        width: 220px;
        background-color: #F7F9FA;
        height: 60px;
        font-size: 16px;
        text-align: center;
        font-weight: 600;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #dedede;
        float: right;
        border-right: 0px;

        @media(max-width:767px) {
            width: 180px;
        }
    }

    .header__dashbord , .header__link {
        float: right;
        background-color: #F7F9FA;
        height: 60px;
        font-size: 16px;
        width: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid #dedede;
        cursor: pointer;
        font-size: 20px;
    }

    .header__link {
        border-width: 0px;
        background-color: #fff;
        color: #888;
        height: 59px;
        width: 40px;
        float: left;
        text-decoration: none;

        &:hover {
            color: #62cb31;
        }
    }
}